@import url(https://fonts.googleapis.com/css2?family=Teachers:ital,wght@0,400..800;1,400..800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300..800;1,300..800&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.note {
  padding: 25px;
  height: 280px;
  width: 260px;
  display: flex;
  flex-direction: column;
  border-radius: 30px;
  position: relative;
}

.note_text {
  flex: 1 1;
  height: 80%;
  resize: none;
  background-color: transparent;
  font-size: 1rem;
  line-height: 1.875rem;
  outline: none;
  border: none;
}

.note_footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  -webkit-user-select: none;
          user-select: none;
}

.note_footer p {
  opacity: 0.7;
}

.note_footer img {
  cursor: pointer;
  opacity: 0;
  transition: 300ms;
}

.note:hover .note_footer img {
  opacity: 1;
}

.note_footer img:hover {
  animation: shake 0.72s;
  scale: 1.2;
}

@keyframes shake {
  0% {
    transform: rotate(0);
  }
  15% {
    transform: rotate(10deg);
  }
  30% {
    transform: rotate(-15deg);
  }
  45% {
    transform: rotate(8deg);
  }
  60% {
    transform: rotate(-8deg);
  }
  75% {
    transform: rotate(4deg);
  }
  85% {
    transform: rotate(-4deg);
  }
  92% {
    transform: rotate(1deg);
  }
  100% {
    transform: rotate(0);
  }
}

.noNotes-heading {
    color: grey;
    font-family: "Teachers", sans-serif;
    font-weight: 600;
    font-size: 2rem;
    word-spacing: 5px;
}
.note-container {
  height: 100%;
  width: 100%;
}

.note-container h2 {
  margin-bottom: 20px;
  -webkit-user-select: none;
          user-select: none;
}

.note-container_notes {
  height: 100%;
  display: flex;
  flex-wrap: wrap;
  grid-gap: 20px;
  gap: 20px;
  overflow-y: scroll;
}

.noNotes-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  text-align: center;
}
.sidebar {
  display: flex;
  flex-direction: column;
  grid-gap: 40px;
  gap: 40px;
}

.sidebar img {
  width: 60px;
  cursor: pointer;
  transition: all 200ms;
}

.sidebar img:hover {
  scale: 1.15;
}

.sidebar_list {
  display: flex;
  flex-direction: column;
  grid-gap: 20px;
  gap: 20px;
  align-items: center;
  height: 0;
  transition: 300ms;
}

.sidebar_list_active {
  height: 300px;
}

.sidebar_list_item {
  border-radius: 50%;
  height: 24px;
  width: 24px;
  list-style: none;
  cursor: pointer;
  transition: all 200ms;
}

.sidebar_list_item:hover {
  scale: 1.4;
}
.App {
  height: 100vh;
  padding: 40px;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  transition: all 200ms;
}

.searchbox {
  margin-top: 1rem;
  margin-bottom: 2rem;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
}

.searchbox input {
  width: 60%;
  margin: 0 auto;
  outline: none;
  border-radius: 8px;
  padding: 15px;
  font-size: large;
  font-family: "Open Sans", sans-serif;
  border: none;
  box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
}

.searchbox .clear-btn {
  position: absolute;
  right: 25%;
  cursor: pointer;
  transition: all 200ms;
}

.searchbox .clear-btn:hover {
  scale: 1.6;
}

.notes-container {
  height: 80%;
  width: 100%;
  display: flex;
  grid-gap: 20px;
  gap: 20px;
}

.custom-scroll::-webkit-scrollbar {
  width: 8px;
}
.custom-scroll::-webkit-scrollbar-track {
  background-color: transparent;
}

.custom-scroll::-webkit-scrollbar-thumb {
  border-radius: 20px;
  background-color: lightgray;
  -webkit-transition: 300ms;
  transition: 300ms;
}

.custom-scroll::-webkit-scrollbar-thumb:hover {
  background-color: gray;
}

textarea::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
	background-color: #F5F5F5;
}

textarea::-webkit-scrollbar {
  width: 4px;
	background-color: #F5F5F5;
}

textarea::-webkit-scrollbar-thumb {
  background-color: #000000b0;
	border: 2px solid #555555;
}

